import { _decorator, sys } from 'cc';
import { EventName } from '../const/eventName';
import { App } from '../core/app';
import { GlobalFuncHelper } from '../utils/globalFuncHelper';
import { StorageHelper, StorageHelperKey } from '../utils/storageHelper';
import { Advertise } from './advertise';
export var IsGetInfo = false;
export class WxManager {
    private wxCode = '';
    public init() {
        App.event.on(EventName.Game.Share, this._evtShareToOthers, this);
        this._wxLogin();
    }

    /**
     * 
     */
    _wxLogin() {
        if (sys.platform != sys.Platform.WECHAT_GAME) return;
        console.log('start_login');
        let self = this;
        // @ts-ignore
        wx.login({
            success: function (res) {
                console.log('loginSuccess');
                self.wxCode = res.code
                self._wxGetUserState()
            }.bind(this)
        })
    }

    /**
     * 
     */
    private _wxGetUserState() {
        console.log('_wxGetUserState');
        let self = this;
        // @ts-ignore
        wx.getSetting({
            fail: function (res) {
                console.log('_wxGetUserState1');
                self._wxPrivacy()
            }.bind(this),

            success: function (res) {
                console.log('_wxGetUserState2');
                if (res.authSetting['scope.userInfo']) {
                    console.log('');
                    // self.getUserInfo();
                    // self._wxPrivacy();
                    self._requirePrivaceAuthorize();
                }
                else {
                    self._wxPrivacy();
                }
            }.bind(this)
        })
    }


    /**  */
    private _wxPrivacy() {
        let self = this;

        // wx.openPrivacyContract({
        //     success: function () {
        self._requirePrivaceAuthorize();
        //     }, // 
        //     fail: function () {
        //     }, // 
        //     complete: function () { }
        // })
    }

    private _requirePrivaceAuthorize() {
        let self = this;
        // @ts-ignore
        wx.requirePrivacyAuthorize({
            success: function (res) {
                console.log("-------- privacy --------")
                console.log(res);
                // API
                self._wxGetUserInfo();
            },
            fail: function () {
            },
            complete: function () { }
        })
    }

    /**
     * 
     */
    private _wxGetUserInfo() {
        console.log(' wxgetuserInfo');
        let self = this;
        // @ts-ignore
        let WXInfoButton = wx.createUserInfoButton({
            type: 'text',
            text: 'click',
            style: {
                left: 0,
                top: 0,
                width: 750,
                height: 1443,
                backgroundColor: '',
                borderColor: '#FFFFFF',
                borderWidth: 0,
                borderRadius: 0,
                color: '#FFFFFF',
                textAlign: 'center',
                fontSize: 1,
                lineHeight: 1400,
            }
        })
        WXInfoButton.onTap((res) => {
            if (res.errMsg.indexOf('auth deny') > -1 || res.errMsg.indexOf('auth denied') > -1) {
                // 
                self._guideActive();
            } else {
                self._setUserData(res)
            }
            WXInfoButton.hide();
        })
        WXInfoButton.show();
    }


    _guideActive() {
        let self = this;
        // @ts-ignore
        wx.showModal({
            title: '',
            content: '',
            cancelText: '',
            showCancel: true,
            confirmText: '',
            success: (function (res) {
                if (res.confirm) {
                    // @ts-ignore
                    wx.openSetting({
                        success: (function (res) {
                            if (res.authSetting['scope.userInfo'] === true) {
                                self.getUserInfo()
                            }
                        }).bind(this)
                    })
                } else {
                }
            }).bind(this)
        })
    }

    /**
     * 
     */
    getUserInfo() {
        console.log('getUserInfo1');
        let self = this;
        // @ts-ignore
        wx.getUserProfile({
            fail: function (res) {
                console.log('getUserInfo3');
                console.log(res);
                if (res.errMsg.indexOf('auth deny') > -1 || res.errMsg.indexOf('auth denied') > -1) {
                    // 
                    self._guideActive();
                }
            }.bind(this),
            success: function (res) {
                console.log('getUserInfo2');
                self._setUserData(res);
            }.bind(this)
        })
    }

    /**
     * 
     */
    private _shareFunc() {
        // @ts-ignore
        wx.showShareMenu({
            withShareTicket: true,
            menus: ['shareAppMessage', 'shareTimeline']
        });
        // @ts-ignore
        let imgUrl = canvas.toTempFilePathSync({
            x: 10,
            y: 10,
            width: 700,
            height: 1100,
            destWidth: 400,
            destHeight: 300
        });
        // @ts-ignore
        wx.onShareAppMessage(function () {
            return {
                title: '',
                imageUrl: imgUrl,
            }
        });
        // @ts-ignore
        wx.onShareTimeline(function () {
            return {
                title: '', //
                imageUrl: imgUrl,
            }
        })
    }

    /**
    * 
    */
    private _evtShareToOthers(lv: number) {
        if (sys.platform == sys.Platform.WECHAT_GAME) {
            // @ts-ignore
            let tempFilePath = canvas.toTempFilePathSync({
                x: 10,
                y: 10,
                width: 700,
                height: 1100,
                destWidth: 400,
                destHeight: 300
            })
            // @ts-ignore
            wx.shareAppMessage({
                title: `${lv}`,
                imageUrl: tempFilePath
            })
        }
        //
        setTimeout(() => {
            this.pushReward();
        }, 2000);
    }

    public pushReward() {
        GlobalFuncHelper.setGold(100);
        // let count = +StorageHelper.getData(StorageHelperKey.ToolTime, 0);
        // StorageHelper.setData(StorageHelperKey.ToolTime, count + 1);
        let count = +StorageHelper.getData(StorageHelperKey.RefreshTools, 0);
        StorageHelper.setData(StorageHelperKey.RefreshTools, count + 1);
        count = +StorageHelper.getData(StorageHelperKey.TipsTool, 0);
        StorageHelper.setData(StorageHelperKey.TipsTool, count + 1);
        count = +StorageHelper.getData(StorageHelperKey.ReplaceTool, 0);
        StorageHelper.setData(StorageHelperKey.ReplaceTool, count + 1);
        // count = +StorageHelper.getData(StorageHelperKey.ToolReturn, 0);
        // StorageHelper.setData(StorageHelperKey.ToolReturn, count + 1);
        App.event.emit(EventName.Game.ToolCountRefresh);
        App.event.emit(EventName.Game.UpdataGold);
        App.view.showMsgTips("");
    }

    public ShareToOthers(lv: number) {
        if (sys.platform == sys.Platform.WECHAT_GAME) {
            // @ts-ignore
            let tempFilePath = canvas.toTempFilePathSync({
                x: 10,
                y: 10,
                width: 700,
                height: 1100,
                destWidth: 400,
                destHeight: 300
            })
            // @ts-ignore
            wx.shareAppMessage({
                title: `${lv}`,
                imageUrl: tempFilePath
            })
        }
    }

    private _setUserData(res) {
        console.log("------------------res------------");
        console.log(res);
        let self = this;
        let info = res.userInfo;
        /*  */
        self._shareFunc();
        console.log('  ');
        if (info && info.nickName) {
            let data = { name: info.nickName, icon: info.avatarUrl };
            console.log(data);
            App.user.rankData.name = info.nickName;
            App.user.rankData.icon = info.avatarUrl;
            Advertise.init(); // 
        }
        // Net.init(() => {
        //     console.log('  ');
        //     let data = { name: info.nickName, icon: info.avatarUrl };
        //     console.log(data);
        //     Net.sendMsg(data, Router.rut_login);
        // });
    }

    public setWxCloudRankData(lv: number) {
        // @ts-ignore
        wx.setUserCloudStorage({
            KVDataList: [{ key: 'level', value: lv }], //KVDataList: KKey VValue DateList  KV  , // , 
            success: () => {
                console.log('setwxcloudrankdata success');
            }
        })
    }
}


export let WxMgr = new WxManager();
