import { sys } from "cc";
import { Language, LanguageKey } from "../const/enumConst";
import { App } from "../core/app";
import { Net } from "../net/net";
import { Router } from "../net/routers";
import { Logger } from "db://assets/core_tgx/base/log/Logger";

/** key */
class StorageKey {
    Audio_Open = 'Audio_Open';
    Audio_Volume = 'Audio_Volume';
    /**  */
    Music_Status = "Music_Status";
    /**  */
    Music_Eff_Status = "Music_Eff_Status";
    /**  */
    Zhen_Dong_Status = "Zhen_Dong_Status";
    /** ()ID */
    LevelId = "LevelId";
    /**  */
    StarScore = "StarScore";
    /**  */
    LevelReward = "LevelReward";
    /**  */
    Gold = "Gold";
    /**   */
    RefreshTools = "RefreshTools";
    /**   */
    TipsTool = "TipsTool";
    /**   */
    ReplaceTool = "ReplaceTool";
    Date = "Date";
    /**  */
    LuckSpinTimes = "LuckSpinTimes";
    /** 5 */
    isGotLuckBox = "isGotLuckBox";
    /**  */
    SignTimes = "SignTimes";
    /**  */
    isGotSignReward = "isGotSignReward";
    /**  */
    MyFish = "MyFish";
    /**  */
    PaoLevel = "PaoLevel";
    GuidFirst = "GuidFirst";
}

class Helper {
    /**  */
    initData() {
        //
        if (!this.getData(StorageHelperKey.Music_Status)) {
            this.setBooleanData(StorageHelperKey.Music_Status, true)
        }
        //
        if (!this.getData(StorageHelperKey.Music_Eff_Status)) {
            this.setBooleanData(StorageHelperKey.Music_Eff_Status, true)
        }
        //
        if (!this.getData(StorageHelperKey.Zhen_Dong_Status)) {
            this.setBooleanData(StorageHelperKey.Zhen_Dong_Status, true)
        }
        //  1
        if (!this.getData(StorageHelperKey.LevelId)) {
            this.setData(StorageHelperKey.LevelId, 1)
        }
        // 
        if (!this.getData(StorageHelperKey.Gold)) {
            this.setData(StorageHelperKey.Gold, 500)
        }
        // 
        if (!this.getData(StorageHelperKey.RefreshTools)) {
            this.setData(StorageHelperKey.RefreshTools, 10)
        }
        if (!this.getData(StorageHelperKey.TipsTool)) {
            this.setData(StorageHelperKey.TipsTool, 10)
        }
        // 
        if (!this.getData(StorageHelperKey.ReplaceTool)) {
            this.setData(StorageHelperKey.ReplaceTool, 10)
        }

        // 
        if (!this.getData(StorageHelperKey.StarScore)) {
            this.setData(StorageHelperKey.StarScore, 0)
        }
        if (!this.getData(StorageHelperKey.PaoLevel)) {
            this.setData(StorageHelperKey.PaoLevel, 1)
        }
    }

    getBooleanData(key: string, defData?: any) {
        let data = this.getData(key, defData);
        if (!data) {
            return false;
        }
        return !!+data;
    }

    setBooleanData(key: string, defData: boolean) {
        // '1'true, '0'false
        let data = defData ? '1' : '0';
        this.setData(key, data);
        if (key == StorageHelperKey.Music_Status) {
            if (defData) {
                App.audio.resumeMusic();
            }
            else {
                App.audio.stopMusic();
            }
        }
    }

    getData<T>(key: string, defData?: T): any {
        if (!key) {
            Logger.error("StorageHelper key");
            return;
        }

        let data = sys.localStorage.getItem(key);
        if (!data) {
            return defData;
        }

        let pObject: T;
        try {
            pObject = JSON.parse(data);
        } catch (e) {
            Logger.error(`, data=${data}`);
            pObject = null;
        }
        if (pObject) {
            return pObject
        }
        return data;
    }

    setData(key: string, data: any) {
        if (!key) {
            Logger.error("StorageHelper key");
            return;
        }
        if (!data && data != 0) {
            Logger.error("StorageHelper not data");
            return;
        }

        let dataStr: string = '';
        try {
            dataStr = JSON.stringify(data);
        } catch (e) {
            Logger.error(`, data=${data}`);
            dataStr = null;
        }
        if (dataStr) {
            data = dataStr;
        }
        sys.localStorage.setItem(key, data);
    }
}

export let StorageHelper = new Helper();
export let StorageHelperKey = new StorageKey();