import { sys } from "cc";
import { App } from "../core/app";
import { WxMgr } from "./wxManager";

export class Ads {
    public bannerAdv = null;//banner
    public videoAdv = null;//
    public interstitialAd = null;//

    private bannerId: string = "adunit-f6703c590ddf0256";// bannerid
    private videoId: string = "adunit-f57876a79b9b8840";// id
    private interstitialId: string = "adunit-8abcfe05f1ccfe37";// rid
    constructor() {
        this.init();
    }

    init() {
        if (sys.platform != sys.Platform.WECHAT_GAME) return;
        console.log('--------------init ads-----------------');
        // @ts-ignore
        let winSize = wx.getSystemInfoSync();//size
        //  Banner 
        let bannerWidth = 300
        let bannerHeight = 80
        // @ts-ignore
        let bannerAdv = wx.createBannerAd({
            adUnitId: this.bannerId,//idbannerID
            adIntervals: 30,//30S
            style: {
                left: (winSize.windowWidth - bannerWidth) / 2,
                top: winSize.windowHeight - bannerHeight,
                width: bannerWidth,
            },
        })
        this.bannerAdv = bannerAdv;
        //banner
        bannerAdv.onResize((res) => {
            bannerAdv.style.top = winSize.windowHeight - bannerAdv.style.realHeight - 1;
        })
        // //  Banner 
        // bannerAdv.show();//
        //
        bannerAdv.onError((err) => {
            console.log(err);
        })

        // @ts-ignore 
        let videoAdv = wx.createRewardedVideoAd({
            adUnitId: this.videoId//idID
        })
        videoAdv.onError((err) => {
            console.log(err);
        })
        this.videoAdv = videoAdv;
        // 
        // @ts-ignore
        if (wx.createInterstitialAd) {
            // @ts-ignore
            let interstitialAd = wx.createInterstitialAd({
                adUnitId: this.interstitialId//idID
            })
            this.interstitialAd = interstitialAd;
        }
    }
    //banner
    showBannerAds() {
        if (sys.platform != sys.Platform.WECHAT_GAME) return;
        //  Banner 
        // @ts-ignore
        this.bannerAdv.show();
    }

    // banner
    hideBannerAds() {
        if (sys.platform != sys.Platform.WECHAT_GAME) return;
        // @ts-ignore
        this.bannerAdv.hide();
    }

    //
    showVideoAds() {
        if (sys.platform != sys.Platform.WECHAT_GAME) {
            // App.view.showMsgTips("");
            return;
        }
        //  Video 
        // @ts-ignore
        let videoAdv = this.videoAdv;
        // 
        // @ts-ignore
        videoAdv.show().catch(() => {
            // 
            // @ts-ignore
            videoAdv.load()
                // @ts-ignore
                .then(() => videoAdv.show())
                .catch(err => {
                    console.log(' ')
                })
        })
        // @ts-ignore
        //
        videoAdv.onError((err) => {
            console.log(err);
        })

        // @ts-ignore
        videoAdv.onClose((res) => {
            if (!videoAdv) return;
            // @ts-ignore
            videoAdv.offClose();//NN
            //
            if (res && res.isEnded || res === undefined) {
                //
                WxMgr.pushReward();

            } else {
                //
            }
        })
    }

    // 
    showInterstitialAds() {
        if (sys.platform != sys.Platform.WECHAT_GAME) return;
        let interstitialAd = this.interstitialAd;
        // 
        if (interstitialAd) {
            // @ts-ignore
            interstitialAd.show().catch((err) => {
                console.error(err)
            })
        }

    }
}

export let Advertise = new Ads();